import React from 'react'

const About=()=> {
    return (
        <>
        <div className="main-heading my-5">
            <div className="text-center">

                <h1 className="display-4"  id="About">About Us</h1>
                <hr className="w-25 mx-auto"></hr>
                <div class="container my-5 ">
                    <div class="row">
                        <div class="col-lg-6 col-md-6 col-12 col-xxl-6 mx-auto">
                            <img className="about_img img-fluid" src="https://lh3.googleusercontent.com/AOAg2ke_WojjFTD_lYd8-PuYK5WJZupSQe79l5lOlBKBTseqPPX36W2WxFqIqiHkLsg=s200" alt="About image"/>
                        </div>

                        <div class="col-lg-6 col-md-6 col-12 col-xxl-6 d-flex flex-column justify-content-md-center align-items-center ">
                        <h1>Online Food</h1>
                            <p className="font-smaller ">Traditionally, people had to make calls to place orders or drive to the restaurants for a take-out, then wait for the food to be prepared and delivered. Sometimes, placing an order on the phone means that there could be mistakes in order. Clearly, these aren't really the best solutions to order food from restaurants especially for people with busy lifestyles.The best solution is switching over to online ordering</p>
                            <button type="button" className="btn btn-outline-success " data-bs-toggle="tooltip " data-bs-placement="right" title="Clich here to see more">Check more</button>
                            
                        </div>
                    </div>
                        
                </div>
            </div>
        </div>

        </>
    
    )
}

export default About;
