import React from 'react';

const Contact=() =>{
    return (
        <>
        <div className="main-heading my-5 t py-4">
            <div className="text-center">
                <h1 id="Contact" className="display-4">Contact Us</h1>
                <hr className="w-25 mx-auto"></hr>
            </div>

            <div className="container my-5 ">
                <div className="row gy-2 gx-2">
                    <div className="col-lg-8 col-md-8 col-10 col-xxl-8 mx-auto">
                        <form>
                            <div className="mb-3">
                                <label for="exampleInputPassword1" className="form-label">Name</label>
                                <input type="text" className="form-control" id="exampleInputPassword1" placeholder="Enter your name"/>
                            </div>

                            <div className="mb-3">
                                <label for="exampleInputEmail1" className="form-label">Email address</label>
                                <input type="email" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp"  placeholder="name@example.com"/>
                                <div id="emailHelp" className="form-text">We'll never share your email with anyone else.</div>
                            </div>
               

                            <div className="mb-3">
                                <label className="form-label pe-4">Gender</label>
                                <div class="form-check form-check-inline">
                                    <input class="form-check-input" type="radio" name="inlineRadioOptions" id="inlineRadio1" value="option1"/>
                                    <label class="form-check-label" for="inlineRadio1">Male</label>
                                </div>
                                <div class="form-check form-check-inline">
                                    <input class="form-check-input" type="radio" name="inlineRadioOptions" id="inlineRadio2" value="option2"/>
                                    <label class="form-check-label" for="inlineRadio2">Female</label>
                                </div>
                            </div>
                            
                            <div className=" mb-3">
                                <label for="exampleDataList" className="form-label">Datalist example</label>
                                <input className="form-control" list="datalistOptions" id="exampleDataList" placeholder="Type to search..."/>
                                    <datalist id="datalistOptions">
                                    <option value="India"/>
                                    <option value="San Francisco"/>
                                    <option value="New York"/>
                                    <option value="Seattle"/>
                                    <option value="Los Angeles"/>
                                    <option value="Chicago"/>
                                </datalist>
                            </div>
                            
                            <div className=" mb-3">
                                <div class="mb-3">
                                    <label for="formFile" class="form-label">Default file input example</label>
                                    <input class="form-control" type="file" id="formFile"/>
                                </div>
                            </div>

                            <div className="mb-3">
                                <label for="exampleFormControlTextarea1" className="form-label">Example textarea</label>
                                <textarea className="form-control" id="exampleFormControlTextarea1" rows="3"></textarea>
                            </div>

                            <div className="mb-3 form-check">
                                <input type="checkbox" className="form-check-input" id="exampleCheck1"/>
                                <label className="form-check-label" for="exampleCheck1">Check me out</label>
                            </div>

                            <button type="submit" className="btn btn-primary">Submit</button>
                        </form>
                    </div>         
                </div>         
            </div>  

        </div>         
        </>
    )
}

export default Contact;
