import React from 'react';

const Services=()=> {
    return (
        <>
        <div className="main-heading my-5 bg-light py-4">
            <div className="text-center">
                <h1 id="Services" className="display-4">Services</h1>
                <hr className="w-25 mx-auto"></hr>
                <div className="container services my-5 ">
                    <div className="row">

                        <div className="col-lg-4 col-md-8 col-12 col-xxl-4 mx-auto my-1 box">
                            <div className="row g-0">
                                <div className="col-md-4">
                                    <img className="img-fluid" src="https://i.pinimg.com/736x/df/68/d1/df68d15c4dd26b04df39cd2b1c203ed6.jpg" alt="Services image"/>
                                </div>
                                <div className="col-md-8">
                                    <div className="card-body">
                                        <h5 className="card-title">Hygenic</h5>
                                        <p className=" fs-6">Safety is the keyword in every consumer’s mind. It has become imperative to adhere to mandatory safety instructions.</p>
                                        <p className="card-text"><small className="text-muted">Last updated 3 mins ago</small></p>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className="col-lg-4 col-md-8 col-12 col-xxl-4 mx-auto my-1 box">
                            <div className="row g-0">
                                <div className="col-md-4">
                                    <img className="img-fluid" src="https://image.shutterstock.com/image-vector/cute-bakery-chef-boy-girl-260nw-1726455940.jpg" alt="Services image"/>
                                </div>
                                <div className="col-md-8">
                                    <div className="card-body">
                                        <h5 className="card-title">Fast Delivery</h5>
                                        <p className=" fs-6">With changing situations during the lockdown, it is important to keep your customers up to date on your delivery policies that might affect their orders.</p>
                                        <p className="card-text"><small className="text-muted">Last updated 3 mins ago</small></p>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className="col-lg-4 col-md-8 col-12 col-xxl-4 mx-auto my-1 box">
                            <div className="row g-0">
                                <div className="col-md-4">
                                    <img className="img-fluid" src="https://i.pinimg.com/736x/31/64/a1/3164a1ed98b643fea5cf4944089a27fc.jpg" alt="Services image"/>
                                </div>
                                <div className="col-md-8">
                                    <div className="card-body">
                                        <h5 className="card-title">More Stores</h5>
                                        <p className=" fs-6">As countries go under lockdown, offline stores, restaurants are ordered to close. Governments have asked companies and businesses to enforce work from home.</p>
                                        <p className="card-text"><small className="text-muted">Last updated 3 mins ago</small></p>
                                    </div>
                                </div>
                            </div>
                        </div>

                    </div>
                </div>

                <div className="">
                <button type="button" className="btn btn-outline-success ">Check more</button>
                </div>

            </div>
        </div>
        </>
    )
}

export default Services;
