import React from 'react';

const Offers=()=> {
    return (
        <>
        <div className="main-heading my-5 t py-4 ">

            <div className="container-fluid my-5 offer-color">
                <div className="row">
                    <div className="col-lg-12 col-md-12 col-12 col-xxl-12 text-center mx-auto my-5">
                        <h1 className="text-white">Chech out our other website</h1>
                        <p>“All you need is love. But a little chocolate now and then doesn't hurt.”</p>
                        
                            <button type="button" className="btn btn-outline-light" data-bs-toggle="modal" data-bs-target="#exampleModal">
                                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-arrow-right-circle" viewBox="0 0 16 16">
                                    <path fill-rule="evenodd" d="M1 8a7 7 0 1 0 14 0A7 7 0 0 0 1 8zm15 0A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM4.5 7.5a.5.5 0 0 0 0 1h5.793l-2.147 2.146a.5.5 0 0 0 .708.708l3-3a.5.5 0 0 0 0-.708l-3-3a.5.5 0 1 0-.708.708L10.293 7.5H4.5z"/>
                                </svg> Check more
                            </button>
                        
                    </div>
                </div>
            </div>

        </div>

        {/* Model... */}
        <div class="modal fade" id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog modal-dialog modal-dialog-centered">
                <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" id="exampleModalLabel">Title</h5>
                    <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                </div>
                <div class="modal-body">
                    You wiil be redirected to my Github page
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                    <a href="https://github.com/Yogesh0arya" target="_yogesh">
                        <button type="button" class="btn btn-primary">Chech now</button>
                    </a>             
                </div>
                </div>
            </div>
            </div>
        </>
    )
}

export default Offers;
